.aboutMe {
  margin: 0px;
  padding: 0px;
  width: 100%;
  background-color: #65737e;
  /* #a7adba */
}

.aboutMeContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.aboutMe p {
  margin: 0px;
  padding: 0px;
}

.aboutMeLeftSide {
  width: 50%;
}

.aboutMeText {
  padding: 5% 15% 5% 15%;
  line-height: 1.5em;
  float: left;
  font-size: 1.2em;
}

.aboutMeText h2 {
    text-align: center;
    font-size: 36px;
}


.aboutMeText .aboutMeFirstText {
  padding-bottom: 2%;
  padding-top: 5%;
}

.aboutMeImage {
  margin: 0;
  padding: 0;
  width: 50%;
  height: 100%;

}




